import React from 'react';


function NewPost({
  handleChange,
  getTitle,
  getContent,
  handleSubmit,
  input,
  setInput   
}) {
  return (
    <div>
      <form onSubmit={handleSubmit}>
        <label>Title:</label>
        <input
          type="text"
          ref={getTitle}
          onChange={e => setInput({...input, title: e.target.value})}
          size="39"
          required
       />
       <label>Content:</label>
       <textarea
         onChange={e => setInput({...input, content: e.target.value})}
         cols="41"
         rows="8"
         required
         ref={getContent}
       />
       <button>Create Post</button>
      </form>
   </div>
  );
}

export default NewPost;