import React from 'react';

function Post({
  title,
  content,
  editPostId,
  onDelete,
  id
}) {
  return (
    <div>
      <h2>{title}</h2>
      <p>{content}</p>
      <button onClick={() => editPostId(id)}>Edit</button>
      <button onClick={() => onDelete(id)}>Delete</button>
    </div>
  );
}

export default Post;